<template>
    <div class="itemContainer middleFont row">
        <div v-if="item" class="row col-center" @click="goDetail(item)">
            <div v-show="index+1>0" class="index">{{index + 1}}</div>
            <div class="col content middleFont">
                <div v-show="item.PN" class="largeFont textBlue">{{item.PN}}</div>
                <div v-show="item.ADR" class="textGray gap">地址：{{item.ADR}}</div>
                <div v-show="item.P" class="textBlue gap">电话：{{item.P}}</div>
            </div>
        </div>

        <img class="image-navi" @click="navi(item)" src="../../image/icon_navi.png">
    </div>
</template>
<style scoped>
    .image-navi {
        width: 50px;
        height: 50px;
        margin: 10px;
        top: 0;
        bottom: 0;
    }

    .itemContainer {
        background-color: white;
        padding: 10px;
    }

    .itemContainer .content {
        margin-left: 10px;
    }

    .index {
        background-color: #37AEFF;
        height: 30px;
        width: 30px;
        text-align: center;
        line-height: 30px;
        border-radius: 50%;
        color: white;
        font-size: 12px;
    }


</style>
<script>

    export default {
        props: {
            item: Object,
            index: Number
        },
        data() {
            return {}
        },

        methods: {
            navi(item) {
                this.$emit('onNavi', item)
            },
            goDetail(item) {
                this.$router.push({
                    name: 'poiDetail',
                    params: {
                        poiItem: item
                    }
                })
            },
        },

    }
</script>



