<template>
    <div>
        <mt-header :title="this.title"></mt-header>
        <div class="mint-content middleFont soft-scrollable">
            <table class="content bdr-t bdr-b">
                <tr v-for="item in tableContent" v-show="item.value">
                    <td class="infoTd">{{item.key}}</td>
                    <td>{{item.value}}</td>
                </tr>
            </table>
            <grid-view :columns="4"
                       :isSquare="true"
                       ref='gridView'
                       v-show="taskBo.MEDIASTR && taskBo.MEDIASTR.length > 0"
                       class="bdr-t bdr-b">

                <div
                        v-for="(mediaName , index) in getMediaArray(taskBo.MEDIASTR)"
                        v-if="isImage(mediaName)"
                        @click="previewImage(getMediaUrl(mediaName,taskBo.RID))">

                    <img :src="getMediaThumbUrl(mediaName,taskBo.RID)"
                         class="square"
                    />
                    <!--<div class="imgInfo textGray">{{pic.mediaUsage}}</div>-->
                </div>
            </grid-view>

            <div class="gap process bdr-t" v-if="taskBo.TID==1">
                <div class="title bdr-b row white-btn" @click="loadProcess">
                    <span class="col col-center">办理经过</span>
                    <i class=" col-center textGray"
                       v-show="process.state != 1"
                       :class="{'icon-angle-down': process.isExpand, 'icon-angle-right': !process.isExpand}"></i>
                    <i class="col-center textGray icon-spinner icon-spin" v-show="process.state == 1"></i>
                </div>
                <div class="sectionEmpty bdr-b"
                     v-show="process.isExpand"
                     v-if="process.list.length == 0 && process.state == 2">
                    没有办理经过信息
                </div>
                <div v-for="item in process.list"
                     class="item bdr-b"
                     v-else
                     v-show="process.isExpand">
                    <div class="row">
                        <span>{{item.HUMANNAME}}</span>
                        <span class="col textGray"><span v-show="item.UNITNAME">({{item.UNITNAME}})</span></span>
                    </div>
                    <div class="row item-row">
                        <span class="textGray">处置时间：</span>{{item.PTIME | formatDate(item.TID)}}
                    </div>
                    <div class="row item-row" v-show="item.PACTN">
                        <span class="textGray">当前阶段：</span>{{item.PACTN}}
                    </div>
                    <div class="row item-row">
                        <span class="textGray">活动名称：</span>{{item.POPINION}}
                    </div>
                    <div class="row item-row" v-show="item.DETAIL">
                        <span class="textGray">{{item.detailTitle}}：</span>{{item.detailContent}}
                    </div>
                </div>
            </div>
            <div class="gap  process bdr-t" v-else>
                <div class="title bdr-b row white-btn" @click="loadProcess">
                    <span class="col col-center">回复</span>
                    <i class=" col-center textGray"
                       :class="{'icon-angle-down': process.isExpand, 'icon-angle-right': !process.isExpand}"></i>
                    <!--<i class="col-center textGray icon-spinner icon-spin"></i>-->
                </div>
                <div class="sectionEmpty bdr-b"
                     v-show="process.isExpand"
                     v-if="!taskBo.REP||taskBo.REP.length<=0">
                    该问题还未被回复
                </div>
                <div v-for="item in process.list"
                     class="item bdr-b"
                     v-else
                     v-show="process.isExpand">
                    taskBo.REP
                </div>
            </div>
        </div>

        <big-img v-if="showImg" @clickit="viewImg" :imgSrc="imgSrc"></big-img>

        <!--<div class="bottomSection bdr-t row" v-if="showActionBtn">-->
        <!--<div class="button col bdr-r" @click="transist">处置</div>-->
        <!--</div>-->
    </div>
</template>
<style scoped>

    .wechat img {
        display: block;
        margin: 10px auto;
    }

    .bottomSection {
        position: fixed;
    }

    .bottomSection .button {
        height: 100%;
        text-align: center;
    }

    .mint-content {
        padding: 10px 0 50px 0;
    }

    .grid {
        margin-top: 10px;
        padding: 5px;
    }

    .grid > div {
        padding: 5px;
    }

    .grid img {
        height: 100%;
        width: 100%;
    }

    .content, .grid {
        background-color: white;
        display: block;
    }

    .content {
        padding: 10px;
    }

    .infoTd {
        width: 75px;
        color: #929292;
    }

    .imgInfo {
        width: 100%;
        text-align: center;
        margin-top: 5px;
    }

    .process {
        background-color: white;
    }

    .process .title, .process .item {
        padding: 10px;
    }

    .process .item-row {
        margin-top: 5px;
    }
</style>
<script>
    import Util from 'Util'
    import dateUtil from 'DateUtil'
    import {data as gData} from 'zhixin'
    import http from 'Http'
    import sysConfig from 'sysConfig'
    import {bindNavBar} from 'egovanative'

    import {Indicator, Toast} from 'mint-ui'
    import GridView from 'widget/GridView.vue'
    import BigImg from './widget/BigImg.vue';
    import {notSubscribe} from 'wxUtil'
    import {formatTaskBo} from '../helper'

    function defaultData() {
        return {
            title: "",
            taskBo: {},
            tableContent: [],
//            showActionBtn: false,
            showImg: false,
            imgSrc: '',
            process: {
                state: 0,//0 未加载，1 正在加载，2 已加载
                list: [],
                isExpand: false
            }
        }
    }

    export default {
        data() {
            return defaultData()
        },
        filters: {
            formatDate: function (RT) {
                return dateUtil.formatDate(RT, dateUtil.FORMAT_YMDHM)
            }
        },
        methods: {
            init() {
                this.initPageInst(defaultData())

                this.title = "详情"
                let params = this.$route.params
//                this.showActionBtn = params && params.showActionBtn
                this.taskBo = params && params.taskBo || {}

                this.process.isExpand = !(this.taskBo && this.taskBo.TID == 1)
//                formatTaskBo(this.taskBo)

                this.initContent()
//                this.loadDetail()
            },
            initContent() {
                let taskBo = this.taskBo || {}
                this.tableContent = [
                    {
                        key: '案卷号',
                        value: taskBo.TN
                    },
                    {
                        key: '类型',
                        value: taskBo.TID == 1 ? "上报" : (taskBo.TID == 2 ? "投诉" : (taskBo.TID == 3 ? "留言" : ""))
                    },
                    {
                        key: '问题类型',
                        value: taskBo.EventTypeName
                    },
                    {
                        key: '大类名称',
                        value: taskBo.MainTypeName
                    },
                    {
                        key: '小类名称',
                        value: taskBo.SubTypeName
                    },
                    {
                        key: '问题描述',
                        value: taskBo.C
                    },
                    {
                        key: '问题来源',
                        value: taskBo.EventSrcName
                    },
                    {
                        key: '地址',
                        value: taskBo.ADR
                    },
                    {
                        key: '上报时间',
                        value: dateUtil.formatDate(taskBo.RT, dateUtil.FORMAT_YMDHM)
                    }
                ]
            },
            getMediaArray(str) {
                debugger
                return str ? str.split(',') : "".split(',')
            },
            previewImage(mediaUrl) {
                this.showImg = true;
                console.log(mediaUrl);
                // 获取当前图片地址
                this.imgSrc = mediaUrl;
            },
            viewImg() {
                this.showImg = false;
            },
            getMediaUrl(mediaName, reportID) {
                var params = {
                    mediaName: mediaName,
                    product: '11',
                    relationID: reportID + ""
                }
//
                return http.getConfigServerUrl() + "/mobilepublic/inspect/getdata/downloadMedia?functionParams=" + encodeURIComponent(JSON.stringify(params))
            },
            getMediaThumbUrl(mediaName, reportID) {
                var params = {
                    mediaName: mediaName + ".thumb",
                    product: '11',
                    relationID: reportID + ""
                }
//
                return http.getConfigServerUrl() + "/mobilepublic/inspect/getdata/downloadMedia?functionParams=" + encodeURIComponent(JSON.stringify(params))
            },
            getImgHeight() {
                return (this.$refs.gridView.size.childWidth - 10) + 'px'
            },

            getTransactProcess() {
                http.ajax({
                    functionName: 'getPublicDataList',
                    params: {
                        typeID: 16,
                        extends: this.taskBo.RID + "",
                    },
                    start: () => {
                        this.process.state = 1
                    },
                    success: data => {
                        if (data && data.success) {
                            this.process.list = data.data && data.data.list || []
                            this.process.list.forEach(item => {
                                if (item.DETAIL) {
                                    let i = item.DETAIL.indexOf('：')
                                    if (i < 0) {
                                        i = item.DETAIL.indexOf(':')
                                    }
                                    if (i >= 0 && i < 10) {
                                        item.detailTitle = item.DETAIL.slice(0, i)
                                        item.detailContent = item.DETAIL.slice(i + 1)
                                    } else {
                                        item.detailTitle = '意见'
                                        item.detailContent = item.DETAIL
                                    }
                                }
                            })
                        }
                    },
                    final: () => {
                        this.process.state = 2
                    }
                })
            },
            loadProcess() {
                if (this.process.state == 0) {
                    this.getTransactProcess()
                    this.process.isExpand = true
                } else {
                    this.process.isExpand = !this.process.isExpand
                }
            }
        },

        components: {
            'grid-view': GridView,
            'big-img': BigImg
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('案件详情')
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }
</script>



