<template>
    <div class="mint-content">
        <div class="inputContainer">
            <mt-field label="手机号" type="tel" placeholder="请输入手机号" v-model="phoneNum"></mt-field>
            <mt-field label="验证码" type="number" placeholder="请输入验证码" class="verifyCode" v-model="verifyCode">
                <mt-button @click.native="sendSMS" :disabled="disableSendVerify">{{sendVerifyBtnText}}</mt-button>
            </mt-field>
            <mt-field v-if="showPassword" label="密码" placeholder="请输入密码" v-model="password"
                      type="password"></mt-field>
            <mt-field v-if="showPassword" label="确认密码" placeholder="请再次输入密码" v-model="passwordAgain"
                      type="password"></mt-field>
        </div>
        <mt-button class="defaultBtn" type="default" @click.native="register">绑定</mt-button>
    </div>
</template>
<style>
    .verifyCode .mint-field-other {
        margin-left: 10px;
        margin-right: 10px;
        top: 50%;
        transform: translate(0, -50%);
        -webkit-transform: translate(0, -50%);
    }

    .inputContainer .mint-field-clear {
        display: none;
    }

    .inputContainer .mint-cell-value {
        display: block;
    }

    .verifyCode input {
        width: 130px;
    }

    .inputContainer .mint-cell-title {
        width: 70px;
    }
</style>
<style scoped>
    .inputContainer {
        margin-top: 10px;
    }

    .verifyCode button {
        height: 30px;
        font-size: 15px;
        width: 84px;
    }

    .defaultBtn {
        margin-top: 20px;
    }
</style>
<script>
    import {Indicator, Toast} from 'mint-ui';
    import md5 from 'md5';
    import Util from 'Util'
    import Http from 'Http'
    import {data as gData} from 'zhixin'
    import {configUserData} from '../verify'

    function defaultData() {
        return {
            phoneNum: '',
            password: '',
            passwordAgain: '',
            verifyCode: '',
            countDown: 0,
            showPassword: false
        }
    }

    export default {
        data() {
            return defaultData()
        },
        components: {},
        computed: {
            disableSendVerify() {
                return !this.phoneNum || this.countDown > 0
            },
            sendVerifyBtnText() {
                return this.countDown > 0 ? `${this.countDown}s` : '点击获取'
            }
        },
        methods: {
            init() {
                this.initPageInst(defaultData())
            },
            checkInput() {
                if (!this.phoneNum) {
                    Toast('请填写手机号码')
                    return false
                }
                if (!this.verifyCode) {
                    Toast('请填写验证码')
                    return false
                }
                if (this.showPassword) {
                    if (!this.passwordAgain || !this.password) {
                        Toast('请输入密码')
                        return false
                    }
                    if (this.password != this.passwordAgain) {
                        Toast('两次输入的密码不相同')
                        return false
                    }
                }
                return true
            },
            register() {
                if (!this.checkInput()) {
                    return
                }
                let params = {
                    telNum: this.phoneNum,
                    smsValidCode: this.verifyCode,
                    uuID: gData.shareUUID,//分享不为空，公众号中为空
                    appToken: gData.openID,
                    appTypeID: 0,//0:微信 1:qq
                    loginTypeID: 1 //0:token自动验证免登陆 1:填写验证码登录
                }
//                alert(JSON.stringify(params))
                Http.ajax({
                    methodName: '/api/v2/micrologin',
                    params: params,
                    start: () => {
                        Indicator.open()
                    },
                    success: (data) => {
//                        alert(JSON.stringify(data))
                        if (data && data.success) {
                            configUserData(data.data)
                            this.redirect()
                        } else if (data) {
                            alert(data.message)
                        }
                    },
                    final: () => {
                        Indicator.close()
                    }
                })

            },
            sendSMS() {
                this.countDown = 60
                let interval = setInterval(() => {
                    if (--this.countDown <= 0) {
                        clearInterval(interval)
                    }
                }, 1000)

                Http.ajax({
                    methodName: '/api/v2/register/smsvalicode',
                    params: {
                        telNum: this.phoneNum
                    },
                    success: (data) => {
                        if (data && data.success) {
                            Toast("验证码已发送")
                        } else {
                            if (data) {
                                alert(data.message)
                            }
                            clearInterval(interval)
                        }
                    }
                })
            },
            redirect() {
                let params = this.$route.params
                if (params && params.redirect) {
                    this.$router.replace({
                        name: params.redirect
                    })
                    return true
                }
                return false
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('绑定')
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }
</script>


