<template>
    <div style="margin-top: 5px">
        <grid-view class="grid"
                   :columns="4"
                   :minGap="10">
            <img v-for="(iu, index) in imgUrls"
                 class="square grid-img"
                 v-longclick="imageClickEvent(index)"
                 :src="iu"/>
            <div class="square grid-div" @click.stop="addMedia">
                <input id="input" type="file" name="file" accept="image/*" multiple="multiple" @change="onFileChange"
                       style="display: none;width:0;height:0"/>
                <img
                        class="img-add"
                        src="../../image/icon_add_media.png"/>
            </div>
        </grid-view>
        <div class="img-view" @click="closePreview" v-show="isPreview">
            　　　　 <!-- 遮罩层 -->　　　　
            <div class="img-layer"></div>
            　　　　
            <div class="img">
                <img :src="previewImg"/>　　　　
            </div>
            　　
        </div>
    </div>
</template>
<style scoped>
    .grid {
        padding: 0 10px 10px 10px;
        background-color: white;
    }

    .grid-img {
        margin-top: 10px;
        margin-bottom: 10px
    }

    .grid-div {
        margin-top: 10px;
        margin-bottom: 10px
    }

    .img-view {
        position: inherit;
        width: 100%;
        height: 100%;
    }

    .img-add {
        width: 100%;
        height: 100%;
    }

    .img-view .img-layer {
        position: fixed;
        z-index: 999;
        top: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.7);
        width: 100%;
        height: 100%;
        overflow: hidden;
    }

    .img-view .img img {
        text-align: center;
        max-width: 100%;
        display: block;
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        top: 0;
        margin: auto;
        z-index: 1000;
    }

</style>
<script>
    import {Indicator, MessageBox, Toast} from 'mint-ui';
    import Util from 'Util';
//    import jQuery from 'jquery';
    import http from 'Http';
    import lrz from 'lrz';

    import GridView from 'widget/GridView.vue'


    function defaultData() {
        return {
            isPreview: false,
            previewImg: '',
            imgUrls: [],
            formDatas: [],
            fileNames: [],
            hasParams: []//用来确定formdata是否已添加参数，避免重复添加
        }
    }

    export default {
        data() {
            return defaultData()
        },
        computed: {},
        props: {},
        watch: {
            imgUrls: function (val) {
                this.$emit('onImgListChange', this.imgUrls)
            }
        },
        methods: {


            removeMedia(index) {
                let vm = this;
//                let btnArray = ['取消', '确定'];
                MessageBox.confirm('确定删除该图片?').then(function (action) {
                    vm.imgUrls.splice(index, 1);
                    vm.formDatas.splice(index, 1);
                    vm.fileNames.splice(index, 1);

                })
            },

            previewImage(index) {
                let vm = this;
                vm.isPreview = true;
                vm.previewImg = this.imgUrls[index];
            },
            imageClickEvent(index, el) {
                let self = this
                return {
                    longClick() {
                        self.removeMedia(index)
                    },
                    click() {
                        self.previewImage(index)
                    },
                    preventDefault: true
                }
            },
            closePreview: function () {
                let vm = this;
                vm.isPreview = false;
                vm.previewImg = "";
            },
            addMedia() {
                let vm = this;
//                $('input[type=file]').trigger('click');
                document.getElementById("input").click();
                return false;
            },
            onFileChange: function (e) {
                Indicator.open("正在添加图片")
                var files = e.target.files || e.dataTransfer.files;
                if (!files.length) return;
                this.createImage(files, e);
            },
            createImage: function (file, e) {
                let vm = this;
                lrz(file[0], {width: 640, height: 640, fieldName: 'file1'}).then(function (rst) {
                    vm.imgUrls.push(rst.base64);
                    vm.formDatas.push(rst.formData);
                    vm.fileNames.push(rst.origin.name);
                    return rst;
                }).always(function () {
                    // 清空文件上传控件的值
                    e.target.value = null;
                    Indicator.close()
                });
            },
            saveImage: function (reportID, index) {
                let vm = this;

                //数据传输操作
                var xhr = new XMLHttpRequest();
                xhr.open('POST', http.getConfigServerUrl() + "/mobilepublic/post/uploadMedia");

                xhr.onload = function () {
                    var data = JSON.parse(xhr.response);

                    if (xhr.status === 200 && data && data.resultInfo && data.resultInfo.success) {
                        // 上传成功,继续上传其他图片
                        if (vm.imgUrls && index + 1 < vm.imgUrls.length) {
                            vm.saveImage(reportID, index + 1)

                        } else {
                            vm.$emit('onSuccess')
                        }
                    } else {
                        // 处理错误
                        vm.$emit('onError')
                    }
                };

                xhr.onerror = function (err) {
                    vm.$emit('onError')
                };

                // issues #45 提到似乎有兼容性问题,关于progress
                if (xhr.upload) {
                    try {
                        xhr.upload.addEventListener('progress', function (e) {

                        });
                    } catch (err) {

                    }
                }

                var formData = vm.formDatas[index]
                if (this.hasParams && !this.hasParams[index]) {
                    let params = {
                        mediaID: index + 1 + "",
                        remark: "retries_0",
//                        mediaName:item.name,
                        mediaName: this.fileNames[index],
                        dealType: "0",
                        totalParts: "1",
                        mediaType: "0",
                        reportID: reportID + ""

                    }


                    // 添加参数
                    formData.append('functionParams', JSON.stringify(params));
                    this.hasParams.push(true)
                }

                // 触发上传
                xhr.send(formData);
                /* ==================================================== */

            },
            reset() {
                Util.resetObject(this, defaultData())
            }
        },
        components: {
            'grid-view': GridView
        }
    }
</script>



