<template>
    <div>
        <mt-header :title="this.title"></mt-header>
        <div class="mint-content soft-scrollable">
            <div class="sectionEmpty" v-show="isEmpty">没有数据</div>

            <div v-infinite-scroll="loadData"
                 infinite-scroll-disabled="disableLoad"
                 infinite-scroll-distance="10"
                 infinite-scroll-immediate-check="false">
                <div v-for="(item, index) in dataList"
                     @click="goCaseDetail(item)">
                    <case-item
                            :item="item"
                            :index="index"/>
                </div>

            </div>
        </div>
        <!--<div class="bottomSection" @click="goReport">上报</div>-->
    </div>
</template>
<style scoped>
    .mint-content {
        /*bottom: 48px;*/
    }
</style>
<script>
    import {Indicator, MessageBox, Toast} from 'mint-ui'
    import Util from 'Util'
    import {data as gData} from 'zhixin'
    import http from 'Http'
    import sysConfig from 'sysConfig'
    import {baseData, baseComputed, resolveResult, COUNT_PER_PAGE, beforeLoad} from 'ListManager'
    import CaseItem from '../widget/CaseItem.vue'
    import {verifyState} from '../../verify'

    function defaultData() {
        return {
            title: "",
            ...baseData(),
        }
    }

    export default {
        data() {
            return defaultData()
        },
        computed: {
            ...baseComputed
        },
        components: {
            'case-item': CaseItem
        },
        methods: {
            init() {
                Util.resetObject(this, defaultData())
                this.title = "我的上报"
                this.loadData(true);
            },
            goReport() {
                this.$router.push({
                    name: 'report',
                    params: {
                        callback: () => {
                            this.loadData(true)
                        }
                    }
                })
            },
            goCaseDetail(caseItem) {
                this.$router.push({
                    name: 'caseDetail',
                    params: {
                        taskBo: caseItem
                    }
                })
            },
            loadData(isRefresh) {
                beforeLoad.call(this, isRefresh)
                http.ajax({
                    functionName: 'getPublicReportList',
                    params: {
                        cellPhone: gData.publicCellPhone,
                        cityCode: sysConfig.getCityCode(),
                        pageRecCount: COUNT_PER_PAGE,
                        pageNo: this.pageIndex
                    },
                    start: () => {
                        Indicator.open();
                    },
                    success: data => {
                        if (data) {
                            resolveResult.call(this, {
                                result: data,
                                isRefresh: isRefresh
                            })
                        }
                    },
                    final: () => {
                        Indicator.close()
                    }
                })
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('我的上报')
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }
</script>


