<template>
    <div>
        <navi-header>

        </navi-header>
        <div class="mint-content flex">
            <div id="map1"></div>
            <div v-show="hasResult" class="result-pane row" id="r-result"></div>
            <!--<div v-show="isShowInfoPane" class="panel row">-->
            <!--<poi-item-->
            <!--:item="poiList[itemIndex]"></poi-item>-->
            <!--</div>-->
        </div>
    </div>

</template>
<style scoped>
    #map1 {
        flex: 1;
        width: 100%;
    }

    .flex {
        display: flex;
        flex-direction: column;
    }

    .result-pane {
        padding: 5px;
        background-color: white;
        position: relative;
        height: 200px;
        overflow-y: auto;
        overflow-x: hidden;
        bottom: 0;
        z-index: 2;
    }
</style>
<script>
    import {Indicator} from 'mint-ui'
    import NaviHeader from "../widget/NaviHeader.vue"
    import Util from "Util"

    require('baiduMap');

    function defaultData() {
        return {
            startPoint: {},
            endPoint: {},
            hasResult: false
        }
    }

    export default {
        data() {
            return defaultData()
        },
        methods: {
            init() {
                Util.resetObject(this, defaultData())
                let vm = this
                let startPoint = vm.$route.params && vm.$route.params.startPoint
                let endPoint = vm.$route.params && vm.$route.params.endPoint
                if (startPoint && endPoint) {
                    this.startPoint = startPoint
                    this.endPoint = endPoint
                    this.map1.centerAndZoom(startPoint, 18);
                    this.startBusNavi()
                }

            },
            startBusNavi() {
                Indicator.open("正在搜索公交路线")
                let vm = this
                var transit = new BMap.TransitRoute(this.map1, {
                    renderOptions: {map: this.map1}, onSearchComplete: function (result) {
                        Indicator.close()
                        if (transit.getStatus() == BMAP_STATUS_SUCCESS) {
                            vm.hasResult = true
                            var firstPlan = result.getPlan(0);
                            // 绘制步行线路
                            for (var i = 0; i < firstPlan.getNumRoutes(); i++) {
                                var walk = firstPlan.getRoute(i);
                                if (walk.getDistance(false) > 0) {
                                    // 步行线路有可能为0
                                    vm.map1.addOverlay(new BMap.Polyline(walk.getPath(), {lineColor: "green"}));
                                }
                            }
                            // 绘制公交线路
                            for (i = 0; i < firstPlan.getNumLines(); i++) {
                                var line = firstPlan.getLine(i);
                                vm.map1.addOverlay(new BMap.Polyline(line.getPath()));
                            }
                            // 输出方案信息
                            var s = [];
                            s.push(result.getPlan(0).getDescription(true));
                            document.getElementById("r-result").innerHTML = s.join("<br/>");
                        }
                    }
                });
                transit.search(this.startPoint, this.endPoint);

            },
            startDriveNavi() {
                Indicator.open("正在搜索驾车路线")
                let vm = this
                var driving = new BMap.DrivingRoute(this.map1, {
                    renderOptions: {map: this.map1}, onSearchComplete: function (result) {
                        Indicator.close()
                        if (driving.getStatus() == BMAP_STATUS_SUCCESS) {
                            vm.hasResult = true
                            var firstPlan = result.getPlan(0);
                            // 输出方案信息
                            var s = [];
                            // 绘制步行线路
                            for (var i = 0; i < firstPlan.getNumRoutes(); i++) {
                                var route = firstPlan.getRoute(i);
                                var points = []
                                for (var i = 0; i < route.getNumSteps(); i++){
                                    var step = route.getStep(i);
                                    s.push((i + 1) + ". " + step.getDescription());
                                    points.push(step.getPosition())

                                }
                                vm.map1.addOverlay(new BMap.Polyline(points, {lineColor: "green"}));
                            }

                            document.getElementById("r-result").innerHTML = s.join("<br/>");
                        }
                    }
                });
                driving.search(this.startPoint, this.endPoint);

            },
            startWalkNavi() {
                Indicator.open("正在搜索步行路线")
                let vm = this
                var walking = new BMap.WalkingRoute(this.map1, {
                    renderOptions: {map: this.map1}, onSearchComplete: function (result) {
                        Indicator.close()
                        if (walking.getStatus() == BMAP_STATUS_SUCCESS) {
                            vm.hasResult = true
                            var firstPlan = result.getPlan(0);
                            // 输出方案信息
                            var s = [];
                            // 绘制步行线路
                            for (var i = 0; i < firstPlan.getNumRoutes(); i++) {
                                var route = firstPlan.getRoute(i);
                                var points = []
                                for (var i = 0; i < route.getNumSteps(); i++){
                                    var step = route.getStep(i);
                                    s.push((i + 1) + ". " + step.getDescription());
                                    points.push(step.getPosition())
                                }
                                vm.map1.addOverlay(new BMap.Polyline(points, {lineColor: "green"}));
                            }
                            document.getElementById("r-result").innerHTML = s.join("<br/>");
                        }
                    }
                });
                walking.search(this.startPoint, this.endPoint);
            },

        },
        components: {
            "navi-header": NaviHeader
        },

        mounted() {
            this.map1 = new BMap.Map('map1')
            this.map1.addControl(new BMap.NavigationControl({
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
                type: BMAP_NAVIGATION_CONTROL_SMALL
            }))
            this.map1.addControl(new BMap.GeolocationControl());

        },

        beforeRouteEnter(to, from, next) {
            next(vm => {
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }

    }
</script>


