/**
 * ACCESS_TOKEN：
 * https://api.weixin.qq.com/cgi-bin/token?grant_type=client_credential&appid=&secret=
 * TICKET:
 * https://api.weixin.qq.com/cgi-bin/ticket/getticket?access_token=&type=jsapi
 */
import sha1 from 'sha1'
import http from 'Http'
import {Promise} from 'vue'

require('../libs/jweixin-1.2.0')

let TICKET
let AppID
let errorMsg
let lastRefreshTime = currentTime()
let hasRequestServer = false

const NONCE_STR = "Wm3WZYTPz0wzccnW"

function getTicket(forceRefresh) {
    return new Promise((fullfill, reject)=> {
        http.ajax({
            methodName: '/api/v2/wxPublic/getTicket',
            method: 'GET',
            params: {
                refreshFlag: forceRefresh ? 1 : 0
            },
            success: (data)=> {
                if (data.success && data.data) {
                    TICKET = data.data.ticket
                    AppID = data.data.appID
                    lastRefreshTime = currentTime()
                    hasRequestServer = true
                    fullfill()
                } else {
                    reject()
                }
            },
            fail: ()=> {
                reject()
            }
        })
    })
}

function currentTime() {
    return Math.floor(Date.now() / 1000)
}

function needRefreshLocal() {
    return !TICKET || (currentTime() - lastRefreshTime > 7200)
}

function getConfig() {
    let timestamp = currentTime()
    let url = window.location.href
    let str1 = `jsapi_ticket=${TICKET}&noncestr=${NONCE_STR}&timestamp=${timestamp}&url=${url}`
    let str2 = sha1(str1)
    return {
        timestamp: timestamp,
        nonceStr: NONCE_STR,
        signature: str2
    }
}

function retryPolicy(maxRetryTimes) {
    let retryTimes = 0
    return action=> {
        if (retryTimes < maxRetryTimes) {
            retryTimes++
            action(retryTimes)
        }
    }
}

function doConfig() {
    const rPolicy = retryPolicy(2)
    const retry = ()=> {
        rPolicy((retryTimes)=> {
            //请求过Server，依然失败，这次希望Server去刷新ticket
            configImpl(hasRequestServer, retry)
        })
    }
    wx && wx.error(res=> {
        errorMsg = JSON.stringify(res)
        // Util.alert("微信config失败：" + JSON.stringify(res))
        retry()
    })
    hasRequestServer = false
    configImpl(false, retry)
}

function configImpl(force, retry) {
    let delay = ()=> {
        let config = getConfig()
        let obj = {
            debug: false,
            appId: AppID,
            timestamp: config.timestamp,
            nonceStr: config.nonceStr,
            signature: config.signature,
            jsApiList: [
                'checkJsApi',
                'chooseImage',
                'previewImage',
                'uploadImage',
                'downloadImage'
            ]
        }
        wx && wx.config(obj)
    }

    if (force || needRefreshLocal()) {
        getTicket(force).then(delay, retry)
    } else {
        delay()
    }
}

function notSubscribe() {
    return false
}

export {
    doConfig, notSubscribe
}


