<template>
    <div>
        <mt-header :title="this.title"></mt-header>
        <div class="mint-content soft-scrollable none-divider">

            <div class="gap bdr-b">
                <div class="horizontal">
                    <img class="img-tool" src="../../image/tool_date_normal.png" @click="showTimePicker"/>
                    <img class="img-tool" src="../../image/tool_location_normal.png" @click="insertAddress"/>
                    <span class="span-right">{{this.remnant}}/200</span></div>
                <textarea class="default report-field"
                          v-model="reportText"
                          maxlength="200"
                          rows="6"
                          @input="reportTextInput"
                          :placeholder="reportDescHint">

            </textarea>
            </div>

            <div class="smallFont textGray media-tip gap">长按可删除图片</div>
            <add-media
                    class="bdr-b"
                    ref="addMedia"
                    @onImgListChange="onImgListChange($event)"></add-media>


            <mt-cell class="gap bdr-t bdr-b" @click.native="selectAddress" :title="addressTitle" is-link></mt-cell>

            <mt-button class="report-btn" type="default" @click.native="report" :disabled="hasReported">提交</mt-button>
        </div>
        <mt-datetime-picker
                ref="datePicker"
                @confirm="insertDateTime"
                v-model="value"
        >
        </mt-datetime-picker>

    </div>

</template>

<style scoped>
    .gap {
        margin-top: 10px;
    }

    .grid {
        padding: 10px;
        background-color: white;
    }

    .img-tool {
        width: 30px;
        height: 30px;
    }

    .span-right {
        float: right;
    }

    .grid img {
        margin-top: 10px;
    }

    .report-btn {
        width: 90%;
        margin-top: 25px;
        margin-left: 5%;
        margin-bottom: 20px;
    }


</style>

<script>
    //TODO ios can not choose image
    //TODO Close Indicator after leaving current page
    import {Indicator, Toast, DatetimePicker} from 'mint-ui';
    import Util from 'Util'
    import http from 'Http'
    import dateUtil from 'DateUtil'
    import {data as gData, zx} from 'zhixin'
    import AddMedia from '../widget/AddMedia.vue'

    import GridView from 'widget/GridView.vue'
    import {verifyState} from '../../verify'
    import sysConfig from "sysConfig"

    require('baiduMap');

    const REPORT_TYPE_REPORT = 1;//上报
    const REPORT_TYPE_SUGGEST = 2;//投诉
    const REPORT_TYPE_ADVICE = 3;//留言


    function defaultData() {
        return {
            title: '',
            reportText: '',
            selectedPosition: null,
            wxMediaId: null,
            srcList: [],
            reportType: '',
            successHint: '',
            reportDescHint: '',
            remnant: 0,
            value: new Date(),
            hasReported: false


        }
    }

    export default {
        data() {
            return defaultData()
        },
        components: {
            'grid-view': GridView,
            'add-media': AddMedia
        },
        computed: {
            addressTitle() {
                let s = this.selectedPosition
                return s && s.address ? s.address : '选择位置'
            }

//            eventTypeText() {
//                return this.selectedTypeList
//                        .slice(this.selectedTypeList.length - 2)
//                        .map(item=> item.typeName)
//                        .join('  ')
//            }
        },
        watch: {
            reportText(val) {
                this.reportText = Util.removeEmoji(val)

            }
        },
//        created() {
//            this.initWebUploader()
//
//        },

        methods: {
            showTimePicker() {
                this.value = new Date()
                this.$refs.datePicker.open()
            },
            insertAddress() {
                if (!this.selectedPosition || !this.selectedPosition.lat || !this.selectedPosition.lng) {
                    Toast("请先选择位置")
                } else {
                    this.reportText = this.reportText + this.selectedPosition.address
                }

            },
            insertDateTime(value) {

                this.reportText = this.reportText + dateUtil.formatDate(value.getTime(), dateUtil.FORMAT_YMDHM);
//                this.reportText =this.reportText+value;

            },
            reportTextInput() {
                var txtVal = this.reportText.length;

                this.remnant = txtVal < 200 ? txtVal : 200;
            },
            checkInput() {
                if (!this.reportText) {
                    Toast("请填写问题描述")
                    return false
                }
                if (!this.selectedPosition || !this.selectedPosition.lat || !this.selectedPosition.lng) {
                    Toast("请选择位置")
                    return false
                }
                if ((!this.srcList || this.srcList.length == 0) && this.reportType == REPORT_TYPE_REPORT) {
                    Toast("请选择图片")
                    return false
                }
                if (this.srcList.length > 9) {
                    Toast("图片不能多于9张")
                    return false
                }
//                if (this.selectedTypeList.length == 0) {
//                    Toast("请选择类型")
//                    return false
//                }
                return true
            },
            onImgListChange(list) {
                this.srcList = list || []
            },

            report() {
                if (!this.checkInput()) {
                    return
                }

                let params = {

                    personName: gData.publicUserName,
                    cellPhone: gData.publicCellPhone,
                    cityCode: sysConfig.getCityCode(),
                    typeID: this.reportType,
                    latitude: this.selectedPosition.lat * 1e6,
                    longitude: this.selectedPosition.lng * 1e6,
                    address: this.selectedPosition.address,
                    content: this.reportText,
                    mediaNum: this.srcList.length,
                    sourceID: 1,
                    uniqueID: gData.publicCellPhone + new Date().getTime(),
                }

                let reportID = 0
                var vm = this

                http.ajax({
                    functionName: "publicReport",
                    params: params,
                    start: () => {
                        Indicator.open("正在上报")
                    },
                    success: (data) => {
                        if (data && data.success) {
                            reportID = data.data.reportID;
                            if (reportID && reportID > 0) {
                                if (this.srcList && this.srcList > 0) {
                                    vm.$refs.addMedia.saveImage(reportID, 0)
                                    vm.$refs.addMedia.$on('onSuccess', function () {
                                        Indicator.close()
                                        Toast(vm.successHint)
                                        this.hasReported = true
                                        Util.doCallback(vm)
                                        Util.goBack()
                                    })
                                    vm.$refs.addMedia.$on('onError', function () {
                                        Indicator.close()
                                        Toast("上报失败！")
                                    })
                                } else {
                                    Indicator.close()
                                    Toast(vm.successHint)
                                    this.hasReported = true
                                    Util.doCallback(vm)
                                    Util.goBack()
                                }


                            } else {
                                Indicator.close()
                                Toast("上报失败！")
                            }

                        } else {
                            Indicator.close()
//                            alert(data && data.message)
                            Toast("上报失败")
                        }
                    },
                    error: (d1, d2) => {
                        Indicator.close()
                        Toast("上报失败")
                    },
                    final: () => {

                    }
                })

            },

            selectAddress() {
                var vm = this
                this.$router.push({
                    name: 'selectAddress',
                    params: {
                        callback: position => {
                            vm.selectedPosition = position || null
                        },
                        selectedPosition: this.selectedPosition
                    }
                })
            },
            init() {
                this.initPageInst(defaultData())
                let params = this.$route.params
                this.reportType = params && params.reportType
                switch (this.reportType) {
                    case REPORT_TYPE_REPORT:
                        this.title = '上报';
                        this.successHint = "您的上报已提交成功，我们会尽快处理，谢谢您的参与！";
                        this.reportDescHint = "请描述问题发生的情况，并至少添加一张问题相关的图片！";
                        break;
                    case REPORT_TYPE_SUGGEST:
                        this.title = '投诉';
                        this.successHint = "您的投诉已提交成功，我们会尽快处理！";
                        this.reportDescHint = "请详细描述事件发生的内容和投诉对象，以便于我们进行调查！";
                        break;
                    case REPORT_TYPE_ADVICE:
                        this.title = '留言';
                        this.successHint = "您的留言已提交成功，我们会尽快处理！";
                        this.reportDescHint = "请填写您的留言信息！";
                        break;
                    default:
                        this.title = '上报';
                        this.successHint = "您的上报已提交成功，我们会尽快处理，谢谢您的参与！";
                        this.reportDescHint = "请描述问题发生的情况，并至少添加一张问题相关的图片！";
                        break;
                }
                this.$refs.addMedia.reset()
                Util.setTitle(this.title)
                this.initLocation()
//                this.$refs.picker.reset()
            },
            initLocation() {
                let vm = this
                new BMap.Geolocation().getCurrentPosition(function (r) {
                    if (this.getStatus() == BMAP_STATUS_SUCCESS) {
                        let pt = r.point
                        let geoc = new BMap.Geocoder()

                        geoc.getLocation(pt, rs => {
                            if (!rs) {
                                return
                            }
                            var address = rs.address;

                            vm.selectedPosition = {
                                lat: pt.lat,
                                lng: pt.lng,
                                latitude: pt.lat,
                                longitude: pt.lng,
                                address: `${address}`

                            }
                        })
                    }
                }, {enableHighAccuracy: true})
            }

        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('上报')
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }
</script>



