<template>
    <div class="itemContainer bdr-b middleFont row">
        <div class="index">{{index + 1}}</div>
        <div class="col content middleFont">
            <div v-show="item.TN" class="largeFont textBlue">案卷号：{{item.TN}}</div>
            <!--<div class="textGray">{{item.SubTypeName}}</div>-->
            <div class="textGray">{{item.C}}</div>
            <grid-view v-show="item.MEDIASTR && item.MEDIASTR.length > 0"
                       class="mediaContent"
                       :columns="4"
                       :isSquare="true"
                       :minGap="10">
                <!--@click.stop="previewImage(index, item.mediaInfo)"-->
                <img v-show="item.MEDIASTR"
                     class="square"
                     v-for="(mediaName , index) in item.MEDIASTR.split(',')"
                     v-if="isImage(mediaName)"
                     v-lazy="getMediaThumbUrl(mediaName,item.RID)"/>
            </grid-view>
            <div class="textBlue">状态：{{item.SID | getStateName(item.TID)}}</div>
            <div class="textGray">{{item.ADR}}</div>
            <div class="textGray" v-show="item.RT">{{item.RT | formatDate(item.TID)}}</div>
        </div>
    </div>
</template>
<style scoped>
    .itemContainer {
        background-color: white;
        padding: 10px;
    }

    .itemContainer .content {
        margin-left: 10px;
    }

    .index {
        background-color: #37AEFF;
        height: 30px;
        width: 30px;
        text-align: center;
        line-height: 30px;
        border-radius: 50%;
        color: white;
        font-size: 12px;
    }

    .grid img {
        background-color: #ddd;
        margin: 5px 0;
    }

    .grid img[lazy=loading] {
        padding: 20px;
    }

</style>
<script>
    import GridView from 'widget/GridView.vue'
    import http from "Http"
    import dateUtil from "DateUtil"

    export default {
        props: {
            item: Object,
            index: Number
        },
        data() {
            return {}
        },
        filters: {
            formatDate: function (RT, typeID) {
                return dateUtil.formatDate(RT, dateUtil.FORMAT_YMDHM) + "  " + (typeID == 1 ? "上报" : (typeID == 2 ? "投诉" : (typeID == 3 ? "留言" : "")))
            },
            getStateName: function (stateID, typeID) {
                var stateName
                switch (stateID) {
                    case 1:
                        if (typeID == 1) {
                            return "待审核"
                        }
                        return "待处理"
                    case 2:
                        if (typeID == 1) {
                            return "待处理"
                        }
                        return "处理中"
                    case 3:
                        if (typeID == 1) {
                            return "处理中"
                        }
                        return "已处置"
                    case 4:
                        return "已处置"
                }
            }
        },
        methods: {
//            previewImage(index, mediaInfo) {
//                let srcList = mediaInfo.map(item=> item.mediaUrl)
//                let src = mediaInfo[index].mediaUrl
//                wx.previewImage({
//                    current: src,
//                    urls: srcList
//                })
//            }

            getMediaThumbUrl(mediaName, reportID) {
                var params = {
                    mediaName: mediaName + ".thumb",
                    product: '11',
                    relationID: reportID + ""
                }
//
                return http.getConfigServerUrl() + "/mobilepublic/inspect/getdata/downloadMedia?functionParams=" + encodeURIComponent(JSON.stringify(params))
            },
        },
        components: {
            'grid-view': GridView
        },
    }
</script>



