import {Indicator} from 'mint-ui';

function formatTaskBo(taskBo) {
    if (!taskBo) {
        return
    }
    if (!taskBo.recID) {
        taskBo.recID = taskBo.RecID
    }
    if (!taskBo.actDefID) {
        taskBo.actDefID = taskBo.ActDefID
    }
    if (!taskBo.actID) {
        taskBo.actID = taskBo.ActID
    }
    return taskBo
}

function uploadWxMedia(srcList, doneAction) {
    if (!srcList || srcList.length == 0) {
        doneAction([])
        return
    }
    let imageIds = []
    let cloneList = srcList.slice(0)

    Indicator.open('上传多媒体')
    function uploadImpl() {
        wx.uploadImage({
            localId: cloneList[0],
            isShowProgressTips: false,
            success: res=> {
                imageIds.push(res.serverId)
                cloneList.shift()
                if (cloneList.length == 0) {
                    Indicator.close()
                    doneAction(imageIds)
                } else {
                    uploadImpl()
                }
            },
            fail: function (re) {
                alert(JSON.stringify(re))
                Indicator.close()
            }
        })
    }

    uploadImpl()
}

export {
    formatTaskBo, uploadWxMedia
}


