<template>
    <div>
        <mt-header :title="this.poiName">
            <mt-button slot="right" class="confirm-btn" type="default"  :plain="true" size="small"
                       @click.native="switchMode">{{switchButtonName}}
            </mt-button>
        </mt-header>
        <div class="mint-content flex">
            <div id="map"></div>
            <div v-show="isShowInfoPane" class="panel row">
                <poi-item
                        @onNavi="onNavi($event)"
                        :item="poiList[itemIndex]"></poi-item>
            </div>
        </div>
        <div v-show="listMode"
             class="mint-content list"
             infinite-scroll-disabled="disableLoad"
             infinite-scroll-distance="10"
             infinite-scroll-immediate-check="false">
            <div class="no-data gap" v-show="!poiList||poiList.length<=0">
                没有相关数据
            </div>
            <div v-for="(item , index) in poiList">
                <poi-item
                        @onNavi="onNavi($event)"
                        class="bdr-b"
                        :item="item"
                        :index="index"></poi-item>
            </div>

        </div>
    </div>
</template>
<style scoped>
    .no-data{
        width: 100%;
        text-align: center;
    }
    .list {
        z-index: 3;
    }

    .flex {
        display: flex;
        /*min-height: 100vh;*/
        flex-direction: column;
    }

    .panel {
        /*padding: 10px;*/
        background-color: white;
        position: relative;
        bottom: 0;
        z-index: 2;
    }

    #map {
        flex: 1;
    }

    .panel .confirm-btn {
        margin-left: 10px;
    }
</style>
<script>
    import Util from 'Util'
    import {Toast, Indicator} from 'mint-ui'
    import PoiItem from '../widget/PoiItem.vue'
    import http from 'Http'

    require('baiduMap');

    function defaultData() {
        return {
            poiName: "",
            poiType: 0,
            currentPosition: {},
            condition: "",//关键字搜索兴趣点
            poiList: [],//兴趣点列表
            markerList: [],//覆盖物列表
            itemIndex: -1,
            isShowInfoPane: false,
            listMode: false,
            switchButtonName: "列表"
        }
    }

    export default {
        data() {
            return defaultData()
        },
        methods: {
            init() {
                this.removeMarkers()
                Util.resetObject(this, defaultData())
                let vm = this
                let poiName = vm.$route.params && vm.$route.params.poiName
                let poiType = vm.$route.params && vm.$route.params.poiType

                if (poiName && poiType) {
                    Indicator.open("正在搜索周围的" + poiName)
                    this.poiName = poiName
                    this.poiType = poiType
                    Util.setTitle(this.poiName)
                    new BMap.Geolocation().getCurrentPosition(function (r) {
                        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
                            vm.currentPosition = r.point;
                            vm.map.centerAndZoom(r.point, 18);
                            vm.searchPOI(r.point)
                        }
                    }, {enableHighAccuracy: true})
                } else {
                    Util.doCallback(vm)
                    Util.goBack()

                }

            },

            switchMode() {
                if (this.listMode) {
                    this.listMode = false
                    this.switchButtonName = "列表"
                } else {
                    this.listMode = true
                    this.switchButtonName = "地图"
                }
            },
            onNavi(item){
                let vm =this
                this.$router.push({
                    name: 'navi',
                    params: {
                        poiItem: item,
                        startPoint:vm.currentPosition,
                        endPoint:new BMap.Point(item.LON / 1e6, item.LAT / 1e6)

                    }
                })
            },

            refreshMarkers() {
                let vm = this;
                this.removeMarkers()
                this.poiList.forEach(function (value, index, array) {
                    let itemPoint = new BMap.Point(value.LON / 1e6, value.LAT / 1e6)
                    let marker = new BMap.Marker(itemPoint)
                    vm.markerList.push(marker)
                    vm.map.addOverlay(marker)
                    marker.addEventListener("click", function (type, target) {
                        vm.isShowInfoPane = true
                        vm.itemIndex = index
                    })
                })
                if (this.poiList && this.poiList.length > 0) {
                    let itemPoint = new BMap.Point(this.poiList[0].LON / 1e6, this.poiList[0].LAT / 1e6)
                    vm.map.centerAndZoom(itemPoint, 18);
                    vm.isShowInfoPane = true
                    vm.itemIndex = 0
                }

            },
            removeMarkers() {
                let vm = this
                this.markerList.forEach(function (value, index, array) {
                    vm.map.removeOverlay(value)
                    value = null
                })
                this.markerList = []
            },
            searchPOI() {
                let vm = this;
                let params = {
                    typeIDs: vm.poiType,
                    startID: 0,
                    latitude: vm.currentPosition.lat * 1e6,
                    lontitude: vm.currentPosition.lng * 1e6,
                    searchR: 100000000,
                    maxCount: 100,
                    condition: vm.condition
                }
                /**
                 * {
	"resultInfo": {
		"success": true,
		"code": null,
		"message": null,
		"data": {
			"list": [{
				"DD": "",
				"LVL": 0,
				"PT": 4,
				"PID": 2,
				"LON": 1.14431988E8,
				"LUT": null,
				"M": "",
				"ADR": "无位置描述",
				"P": "15527840657",
				"SD": "",
				"BT": "金融港",
				"PN": "厕所12",
				"LAT": 3.045807E7
			}]
		}
	}
}
                 */
                http.ajax({
                    functionName: "searchPublicPOI",
                    params: params,
                    start: () => {

                    },
                    success: (data) => {
                        if (data && data.success) {
                            vm.poiList = data.data.list;
                            if (vm.poiList && vm.poiList.length > 0) {
                                Indicator.close()
                                vm.refreshMarkers()
                            } else {
                                Indicator.close()
                                this.removeMarkers()
                                Toast("您的周围未找到" + vm.poiName + "！")
                            }

                        } else {
                            Indicator.close()
//                            alert(data && data.message)
                            this.removeMarkers()
                            Toast("获取数据失败！")
                        }
                    },
                    error: (d1, d2) => {
                        Indicator.close()
                        this.removeMarkers()
                        Toast("获取数据失败！")
                    },
                    final: () => {

                    }
                })

            }

        },
        computed: {
            address() {
                return this.selectedPosition ? this.selectedPosition.address : '请选择位置'
            }
        },

        mounted() {
            this.map = new BMap.Map('map')
            this.map.addControl(new BMap.NavigationControl({
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
                type: BMAP_NAVIGATION_CONTROL_SMALL
            }))
            this.map.addControl(new BMap.GeolocationControl());


        },
        components:{
            "poi-item" : PoiItem
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }
</script>



