<template>
    <div>
        <mt-header fixed title="选择位置"/>
        <div class="mint-content flex">
            <div id="map"></div>
            <div class="panel row">
                <div class="col col-center">

                    <div>{{address}}</div>
                    <div class="smallFont textGray media-tip gap ">长按地图选择位置</div>
                </div>
                <mt-button class="confirm-btn" type="default"
                           @click.native="confirm">确定
                </mt-button>
            </div>
        </div>
    </div>
</template>
<style scoped>
    .flex {
        display: flex;
        /*min-height: 100vh;*/
        flex-direction: column;
    }

    .panel {
        padding: 10px;
        background-color: white;
        position: relative;
        bottom: 0;
        z-index: 2;
    }

    #map {
        flex: 1;
    }

    .panel .confirm-btn {
        margin-left: 10px;
    }
</style>
<script>
    import Util from 'Util'
    import {Toast} from 'mint-ui'


    require('baiduMap');

    function defaultData() {
        return {
            hasLocated: false,
            selectedPosition: null,
            marker: null
        }
    }

    export default {
        data() {
            return defaultData()
        },
        methods: {
            init() {
                this.removeMarker()
                Util.resetObject(this, defaultData())
                let vm = this
                let sp = vm.$route.params && vm.$route.params.selectedPosition
                if (sp) {
                    this.selectedPosition = sp
                    setTimeout(() => {
                        let point = new BMap.Point(sp.lng, sp.lat);
                        vm.map.centerAndZoom(point, 18);
                        vm.clickPosition(sp)
                    }, 100)
                } else {

                    new BMap.Geolocation().getCurrentPosition(function (r) {
                        if (this.getStatus() == BMAP_STATUS_SUCCESS) {
                            if (!vm.hasLocated) {
                                vm.map.centerAndZoom(r.point, 18)
                                vm.clickPosition(r.point)
                                vm.hasLocated = true
                            }
                        }
                    }, {enableHighAccuracy: true})

                }

            },
            confirm() {
                if (!this.selectedPosition) {
                    Toast('请选择位置')
                    return
                }

                this.selectedPosition.latitude = this.selectedPosition.lat
                this.selectedPosition.longitude = this.selectedPosition.lng

                Util.doCallback(this, this.selectedPosition)
                Util.goBack(true)
            },
            getAddressByPoint(pt) {
                let geoc = new BMap.Geocoder()
                geoc.getLocation(pt, rs => {
                    if (!rs) {
                        return
                    }
                    var address = rs.address;

                    this.selectedPosition = {
                        lat: pt.lat,
                        lng: pt.lng,
                        address: `${address}`

                    }
                })


            },
            clickPosition(pt) {
                if (this.marker) {
                    this.map.removeOverlay(this.marker)
                }
                this.marker = new BMap.Marker(pt)
                this.map.addOverlay(this.marker)
                this.getAddressByPoint(pt)
            },
            removeMarker() {
                if (this.marker) {
                    this.map.removeOverlay(this.marker)
                }
            }
        },
        computed: {
            address() {
                return this.selectedPosition ? this.selectedPosition.address : '请选择位置'
            }
        },
        mounted() {
            this.map = new BMap.Map('map')
//            this.map.disableDragging()
            let vue = this
            this.map.addControl(new BMap.NavigationControl({
                anchor: BMAP_ANCHOR_BOTTOM_RIGHT,
                type: BMAP_NAVIGATION_CONTROL_SMALL
            }))
            this.map.addControl(new BMap.GeolocationControl());
            this.map.addEventListener("longpress", function (e) {
                vue.clickPosition(e.point)
            })


        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('选择地址')
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }
</script>



