<template>
    <div>
        <mt-header title="功能导航">
        </mt-header>
        <div class="mint-content">
            <div v-for="item in pluginList"
                 class="plugin bdr-b"
                 @click="goPlugin(item)">
                {{item.displayName}}
            </div>
        </div>
    </div>
</template>
<style scoped>
    .plugin {
        padding: 20px;
        background-color: white;
    }
</style>
<script>
    import {PluginIdMap} from '../config'
    import Vue from 'vue'
    import Util from 'Util'

    export default {
        data() {
            return {
                pluginList: [{displayName: "上报", PLUGINID: 1}, {displayName: "投诉", PLUGINID: 2}, {
                    displayName: "咨询",
                    PLUGINID: 3
                }, {displayName: "我的案件", PLUGINID: 4}, {displayName: "公共厕所", PLUGINID: 5}, {
                    displayName: "公园景点",
                    PLUGINID: 6
                }]
            }
        },
        methods: {
            goPlugin(plugin) {
                switch (plugin.PLUGINID) {
                    case PluginIdMap.report:
                        this.$router.push({
                            name: "report",
                            params: {
                                reportType: 1
                            }
                        })
                        break
                    case PluginIdMap.suggest:
                        this.$router.push({
                            name: "report",
                            params: {
                                reportType: 2
                            }
                        })
                        break
                    case PluginIdMap.advice:
                        this.$router.push({
                            name: "report",
                            params: {
                                reportType: 3
                            }
                        })
                        break
                    case PluginIdMap.mycase:
                        this.$router.push({
                            name: "reportList",
                        })
                        break
                    case PluginIdMap.poi:
                        this.$router.push({
                            name: "poi",
                            params: {
                                poiName: "公共厕所",
                                poiType: 4
                            }

                        })
                        break
                    case PluginIdMap.poi2:
                        this.$router.push({
                            name: "poi",
                            params: {
                                poiName: "公园景点",
                                poiType: 5
                            }

                        })
                        break
//                    case PluginIdMap.verifyCheck:
//                        Vue.http.post("http://192.168.32.224:8080/zy/api/v2/rec/getTaskDetail?taskNum=201705090004&isJsonp=1")
//                        break
                }
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('功能导航')
                if (vm.isNewPage(to)) {

                }
            })
        }
    }
</script>



