<template>
    <div>
        <mt-header title="详情">
        </mt-header>
        <div class="mint-content soft-scrollable none-divider">
            <img v-if="this.poiItem.M" class="img" :src="imageSrc"/>
            <img v-else class="img" src="../../image/icon_no_poi_img.png"/>
            <div class="main  bdr-t bdr-b">
                <div  class="largeFont textBlue">{{this.poiItem.PN}}</div>
                <div  class="textGray gap">地址：{{this.poiItem.ADR}}</div>
            </div>

            <div class="main  bdr-t bdr-b">
                <div  class="textGray ">状态:</div>
                <div  class="textGreen gap">可用</div>
            </div>

            <div class="detail bdr-t bdr-b  gap" >
                <div  class=" textBlue">简介</div>
                <div class="textGray  gap bdr-t">{{this.poiItem.DD?this.poiItem.DD:this.poiItem.PN}}</div>
            </div>

        </div>
    </div>
</template>
<style scoped>
    .img {
        width: 100%;
        height: 160px;
    }
    .detail{
        padding: 10px;
        width:100%;
        height: 200px;
        background:#FFF
    }
    .main{
        padding: 10px;
        width:100%;
        background:#FFF
    }

</style>
<script>
    import Util from 'Util'
    import {Toast, Indicator} from 'mint-ui'
    import PoiItem from '../widget/PoiItem.vue'
    import http from 'Http'


    function defaultData() {
        return {
            poiItem:{}
        }
    }

    export default {
        data() {
            return defaultData()
        },
        methods: {
            init() {
                Util.resetObject(this, defaultData())
                let vm = this
                let poiItem = vm.$route.params && vm.$route.params.poiItem
                if (poiItem) {
                    this.poiItem = poiItem
                }

            },


        },
        computed: {
            imageSrc() {
                var params = {
                    mediaName: this.poiItem.M,
                    product: '11',
                }
//
                return http.getConfigServerUrl() + "/mobilepublic/inspect/getdata/downloadMedia?functionParams=" + encodeURIComponent(JSON.stringify(params))
            }
        },

        components: {
            "poi-item": PoiItem
        },
        beforeRouteEnter(to, from, next) {
            Util.setTitle("详情")
            next(vm => {
                if (vm.isNewPage(to)) {
                    vm.init()
                }
            })
        }
    }


</script>



