<template>
    <div class="mint-content soft-scrollable">
        <div class="sectionEmpty" v-show="isEmpty">没有数据</div>

        <div v-infinite-scroll="loadData"
             infinite-scroll-disabled="disableLoad"
             infinite-scroll-distance="10"
             infinite-scroll-immediate-check="false">
            <div v-for="(item, index) in dataList"
                 @click="goCaseDetail(item)"
                 :key="item.RecID">
                <case-item :item="item" :index="index"/>
            </div>

        </div>
    </div>
</template>
<style scoped>

</style>
<script>
    import {Indicator, MessageBox, Toast} from 'mint-ui'
    import Util from 'Util'
    import {data as gData} from 'zhixin'
    import http from 'Http'
    import {baseData, baseComputed, resolveResult, COUNT_PER_PAGE, resolveHasMore} from 'ListManager'
    import CaseItem from './widget/CaseItem.vue'
    import {verifyState} from '../verify'

    function defaultData() {
        return {
            ...baseData(),
        }
    }

    export default{
        data(){
            return defaultData()
        },
        computed: {
            ...baseComputed
        },
        components: {
            'case-item': CaseItem
        },
        methods: {
            init() {
                Util.resetObject(this, defaultData())
                this.loadData(true)
            },
            goCaseDetail(caseItem){
                this.$router.push({
                    name: 'caseDetail',
                    params: {
                        taskBo: caseItem,
                        showActionBtn: true,
                        callback: ()=> {
                            this.loadData(true)
                        }
                    }
                })
            },
            loadData(isRefresh){
                http.ajax({
                    functionName: "getTransferedTask_ZX",
                    params: {
                        humanID: gData.humanID,
                        taskListID: "2",
                        pageRecCount: COUNT_PER_PAGE,
                        pageNo: this.pageIndex,
                        startDate: "",
                        endDate: "",
                        keyword: "",
                    },
                    start: ()=> {
                        Indicator.open();
                    },
                    success: data => {
                        if (data && data.success) {
                            resolveResult.call(this, {
                                result: data,
                                isRefresh: isRefresh
                            })
                        } else if (data) {
                            resolveHasMore.call(this, null, isRefresh)
                            Toast(data.message)
                        }
                    },
                    final: () => {
                        Indicator.close()
                    }
                })
            }
        },
        beforeRouteEnter(to, from, next) {
            next(vm => {
                Util.setTitle('案件列表')
                if (vm.isNewPage(to)) {
                    verifyState({
                        from: 'caseList',
                        inst: vm
                    }).then(()=> {
                        vm.init()
                    })
                }
            })
        }
    }
</script>


